export default {
	data() {
		return {
			message:{}
		}
	},
	methods: {
		init(e){
			
			let url = getOrderById;
			url = url.replace("###",e)
			myGET(getOrderById,e)
			.then((res)=>{
				console.log(res)
				this.message=res.data;
			})
			.catch((err)=>{
				
			})
		},
	},
	onLoad(option) {
		console.log(option._id);
		this.init(option._id)
	}
}
